.partners {
  padding: 80px 0;
  background-color: #F4F5F7;

  &__link {
    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }
  }

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 102px;

    @include minidesk {
      column-gap: 30px;
    }
  }

  &__item {
    @include minidesk {
      display: flex;
      justify-content: center;
      grid-column: 3 span;
    }
  }
}
