.recent-news {
  padding-top: 80px;
  padding-bottom: 120px;
  background-color: var(--color-light);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    margin-bottom: 80px;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 255px);
    gap: 30px;
  }

  &__item {
    grid-column: 5 span;

    &:first-child {
      grid-column: 6 span;
      grid-row: 2 span;
    }
  }

  &__title {
    margin-bottom: 60px;
  }
}

.news-article {
  overflow: hidden;
  height: 100%;
  border-radius: 4px;
  background: var(--color-light);
  box-shadow:
    0 80px 80px -20px rgba(154, 156, 165, 0.08),
    0 30px 24px -10px rgba(154, 156, 165, 0.05),
    0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 4px 4px -4px rgba(30, 33, 44, 0.03);

  &__text {
    padding: 20px 25px;
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
  }

  &__descr {
    margin: 0;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-grey);
  }

  &__meta {
    margin-bottom: 17px;
  }

  &__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-dark);
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--color-primary);
    }

    &:active {
      color: var(--color-active);
    }
  }

  &--small {
    .news-article__descr {
      display: none;
    }

    .news-article__text {
      padding: 12px 24px;
    }

    .news-article__title {
      margin-bottom: 9px;
    }
  }
}

.article-meta {
  font-size: 14px;
  line-height: 150%;
  color: var(--color-storm);

  &__item {
    position: relative;
    color: inherit;

    &:not(:last-child) {
      margin-right: 20px;

      &::after {
        content: "";
        position: absolute;
        right: -13px;
        top: 3px;
        width: 1px;
        height: 11px;
        color: inherit;
        background-color: currentColor;
      }
    }

    &--comments {
      padding-left: 25px;
      background-image: url(../img/comments.svg);
      background-position: left center;
      background-size: 16px 16px;
      background-repeat: no-repeat;
    }
  }
}

a.article-meta__item {
  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

  &:hover {
    color: var(--color-primary);
  }

  &:active {
    color: var(--color-active);
  }
}
