.hero {
  &__content {
    max-width: 590px;
  }

  &__next {
    position: absolute;
    right: 32px;
    top: 50%;
    z-index: 30;
    background-color: var(--color-light);
    transform: translateY(-50%);

    @include middledesk {
      display: none;
    }
  }

  &__prev {
    position: absolute;
    left: 32px;
    top: 50%;
    z-index: 30;
    background-color: var(--color-light);
    transform: translateY(-50%);

    @include middledesk {
      display: none;
    }
  }

  &__pag {
    display: flex;
    left: calc((100% - 1260px) / 2) !important;
    bottom: 120px !important;
    counter-reset: pag;

    .swiper-pagination-bullet {
      display: flex;
      flex-direction: column;
      border-radius: 0;
      width: 178px;
      height: auto;
      text-align: left;
      background-color: transparent;
      opacity: 1;

      &::before {
        counter-increment: pag;
        content: "0" counter(pag);
        font-weight: 700;
        font-size: 28px;
        line-height: 150%;
        color: rgba(#fff, 0.3);
        transition: color 0.3s ease-in-out;
      }

      &::after {
        display: none;
      }
    }

    @include middledesk {
      left: 3% !important;
    }

    @include minidesk {
      left: 15px !important;
    }
  }

  &__bar {
    position: relative;
    overflow: hidden;
    border: 100px;
    width: 100%;
    height: 2px;
    background-color: rgba(#fff, 0.3);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-light);
      transform-origin: left;
      transform: scaleX(0);

    }
  }


  &__btns {
    .btn {
      &:not(:last-child) {
        margin-right: 22px;
      }
    }
  }
}

.swiper-pagination-bullet-active {
  &::before {
    color: var(--color-light) !important;
  }

  .hero__bar {
    &::after {
      transform: scaleX(1);
      transition: transform var(--hero-slider-speed) ease-in-out;
    }


  }
}

.hero-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.hero-slide {
  padding-top: 220px;
  padding-bottom: 354px;
  color: var(--color-light);
  background-color: var(--color-grey);
  background-image: url('../img/bg-image.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

}
