.portfolio-section {
  background-color: var(--color-light);
  padding: 120px 0;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 50px;
  }

  &__slider-nav {
    @include flex-v-center;
    position: relative;
    z-index: 15;
    user-select: none;
  }

  &__title {
    margin: 0;
    padding-bottom: 12px;
    max-width: 710px;
    color: var(--color-dark);

    @include tablet {
      font-size: 40px;
      line-height: 55px;
    }
  }

  &__learn {
    margin-top: 80px;
  }

  &__items {
    padding: 100px;
    margin: -100px;

    .swiper-slide {
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    .slider-visible {
      opacity: 1;
      visibility: visible;
    }
  }
}

.portfolio-item {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow:
    0 80px 80px -20px rgba(154, 156, 165, 0.08),
    0 30px 24px -10px rgba(154, 156, 165, 0.05),
    0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 4px 4px -4px rgba(30, 33, 44, 0.03);
  border-radius: 4px;
  background: #FFFFFF;
  transition: transform 0.3s ease-in-out;

  &__content {
    @include flex-column-center;
    padding: 10px 10px;
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-dark);
  }

  &__descr {
    font-size: 14px;
    line-height: 150%;
    color: var(--color-storm);
  }

  &__btn {
    // display: none;
    margin-bottom: -68px;
    margin-top: 24px;
    border: 1px solid currentColor;
    border-radius: 4px;
    padding: 13px 31px;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--color-primary);
    transition: 0.3s ease-in-out;
    will-change: margin;
  }

  &:hover {


    .portfolio-item__btn {
      margin-bottom: 0;
    }
  }

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

}
