.form-field {
  @include flex-column-start;

  &__caption {
    font-size: 14px;
    margin-bottom: 8px;
    line-height: 150%;
    color: var(--color-grey);
  }

  &__text {
    margin-bottom: 8px;
  }

  &__input {
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding-left: 27px;
    width: 260px;
    height: 44px;
    background: #F4F5F7;
    line-height: 150%;


    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &--big {
      height: 66px;
    }

    ::placeholder {
      color: #9A9CA5;
    }

  }

  &--big {
    flex-grow: 1;



    .form-field__input {
      width: 100%;

    }
  }

  &--gray {
    width: 100%;

    .form-field__input {
      width: 100%;
      background-color: #f4f5f7;
    }
  }
}
